@import url(https://fonts.googleapis.com/css2?family=LXGW+WenKai+Mono+TC&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #e0e9f5;
}

main {
  margin-top: 7rem;
  text-align: center;
}

h1,
h2,
h3,
p {
  color: #042b5f;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.side-by-side-container {
  display: flex;
  grid-gap: 20px;
  gap: 20px; /* this adds space between the elements, adjust as needed */
}

.side-by-side-container > * {
  flex: 1 1; /* This distributes space equally among the children */
}

.textarea-autosize {
  border: none;
  overflow: hidden;
  resize: vertical; /* Allow manual vertical resizing */
  min-height: 24px; /* Just a minimal height to ensure it starts small */
}

